// z-index site
nav.main {
    z-index: 1;
}
.usk_title {
    width: 100%;
    height: 23px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .line {
        height: 1px;
        background: #ebebeb;
        width: 50%;
    }
    .small_img {
        display: flex;
        flex-shrink: 0;
        width: 65px;
        height: 100%;
        margin: 0 10px;
        background-repeat: no-repeat;
        background-position: center center;
        &.logo {
            background-image: url(../img/usk_small.png);
        }
        &.last_news {
            background-image: url(../img/last-news-small.png);
        }
        &.our_team {
            background-image: url(../img/our-team.png);
        }
    }
}
.empty_line {
    width: 100%;
    height: 1px;
    background: #e0e5ec;
}

section {
    padding: 40px 0;
    .section_title {
        color: #2f2f2f;
        font-size: 22px;
        font-weight: 500;
        margin: 20px 0;
        text-transform: uppercase;
        display: flex;
        width: 100%;
        justify-content: center;
    }
}

.title_page {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 3px solid #ebebeb;
    position: relative;
    @include tablet {
        height: 90px;
        flex-direction: column;
        align-items: flex-start;
    }
    &:before {
        @include pseudo;
        top: 100%;
        left: 0;
        width: 70px;
        height: 3px;
        background-color: $blue2;
    }
    .title_text {
        color: #384452;
        font-size: 26px;
        font-weight: 700;
        text-transform: uppercase;
        @include tablet {
            padding-bottom: 10px;
            order: 2;
        }
        @include mobile {
            font-size: 18px;
        }
    }
}

.hidden {
    display: none !important;
}

.orange_button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: #ff6800;
  border-radius: 4px;
  box-shadow: 0 5px 0 #c85608;
  color: #fff;
  cursor: pointer;
  margin: 0;
  outline: 0;
  padding: 20px 55px;
  transition: all .1s linear;
  text-transform: uppercase;
  font-weight: 600;
  &:active {
    box-shadow: 0 2px 0 #c85608;
    transform: translateY(3px);
  }
}
.blue_button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: #2d62b2;
  border-radius: 4px;
  box-shadow: 0 5px 0 #1b478b;
  color: #fff;
  cursor: pointer;
  margin: 0;
  outline: 0;
  padding: 10px 30px;
  transition: all .1s linear;
  text-transform: uppercase;
  font-weight: 600;
  &:active {
    box-shadow: 0 2px 0 #1b478b;
    transform: translateY(3px);
  }
}
